.bg-color{
  background: linear-gradient(to right,#00ac6d 0%,#00ac6d 30%,#4e84c2 30%,#4e84c2 100%);
}

.navbar .navbar-brand{
  color: white;
  font-size: 2rem;
  font-weight: 500;
}
.navbar-light .nav-item{
  padding: 0 5px;
}

 .dropdown:hover .dropdown-menu {
  display: block;
  margin-top: 0; 
}
.navbar-nav  .dropdown-menu{
  background-color: #365c9a;
  
}
.navbar-nav  .dropdown-menu .dropdown-item{
  color: white;
}
.navbar-nav  .dropdown-menu .dropdown-item:hover{
  
  background-color: #4e84c2;
}
.navbar-light .nav-item .nav-link{
  font-weight: 500 ;
  color: white;
  
}

.navbar-nav .nav-item .nav-link span{
  padding: 3px 6px;
  
  border-radius: 50%;
  border: 1px solid white;
}

.body p {
  color: white;
}

.body h1{
  font-size: 4rem ;
  color: white;
  font-weight: bold;
}
.body .btn{
 margin: 10px 10px;
  padding: 12px 30px;
  
  background-color: white;
  border-radius: 50px;
}
.body .btn a{
  
  text-decoration: none;
  color: rgba(0, 0, 0, 0.884);
}

@media (max-width:991.98px) {
  .bg-color{
    /* background: linear-gradient(to right,#e2e2e2 0%,#9fcebd 30%,#79a3d3 30%,#a1adbb 100%); */
    background: #4e84c2;
  }
  .body{
    padding: 0 10px;
  }
  .body p {
    color: white;
    font-size: 1.1rem;
  }
  
  .body h1{
    font-size: 2rem ;
    color: white;
    font-weight: bold;
  }
  .body .btn-box {
  text-align: center;
  margin-top: 15px;
  }
}